import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

// 引入相关组件

// 懒加载
/*
import a from "./a"  静态引入 
import("./a") 动态引入，而且返回值是Promise对象
*/

import login from "../pages/login/login.vue"
// import index from "../pages/index/index.vue"
// import movie from "../pages/movie/movie.vue"

let index = () => import("../pages/index/index.vue")
let movie = () => import("../pages/movie/movie.vue")

// import movieDetail from "../pages/movieDetail/movieDetail.vue"
// import food from "../pages/food/food.vue" 


let movieDetail=()=>Promise.resolve(import("../pages/movieDetail/movieDetail.vue"))
let food=()=>Promise.resolve(import("../pages/food/food.vue"))


import foodDetail from "../pages/foodDetail/foodDetail.vue"
// import home from "../pages/home/home.vue"
// import cate from "../pages/cate/cate.vue"


import shop from "../pages/shop/shop.vue"
import mine from "../pages/mine/mine.vue"
import search from "../pages/search/search.vue"
// routes 一级路由规则
const routes = [
  {
    path: "/login",
    components:{
      default:()=>import("../pages/login/login.vue"),
      view1:()=>import("../components/ceshi.vue")
    },
    // 命名路由
    name: "login"
  },
  {
    path: "/search",
    component: search,
    name: "搜索页面"
  },
  {
    path: "/index",
    component: index,
    // 二级路由规则，path不加"/"
    children: [
      {
        path: "home",
        component: ()=>import("../pages/home/home.vue"),
        // 元信息
        meta: {
          title: "首页"
        }
      },
      {
        path: "cate",
        component: ()=>import("../pages/cate/cate.vue"),
        meta: {
          title: "分类"
        }
      },
      {
        path: "shop",
        component: shop,
        meta: {
          title: "购物车"
        }
      },
      {
        path: "mine",
        component: mine,
        meta: {
          title: "个人中心"
        }
      },
      // 二级重定向
      {
        path: "",
        redirect: "home"
      }
    ]
  },
  {
    path: "/movie",
    component: movie
  },
  {
    path: "/movieDetail",
    component: movieDetail
  },
  {
    path: "/food",
    component: food
  },
  {
    path: "/foodDetail/:id/:name",
    component: foodDetail
  },
  {
    path: "*",
    redirect: "/login"
  }
]
// 开发环境下，hash和history没有区别
// 生产环境

const router = new VueRouter({
  //路由规则
  routes,
  // mode默认是hash
  //history 不带# ,hash是带#

  mode:"hash"
})

export default router
